module.exports = "<div class=\"type-details\" >\n  <div class=\"col-md-12 no-padding horz-group\">\n    <span class=\"form-label col-md-4 col-sm-4 np\" style=\"margin-right: -4px;\">Validate as:</span>\n    <div class=\"col-md-7 col-sm-6 horz-control np\">\n      <select\n        class=\"select-default\"\n        data-width=\"fit\"\n        class=\"form-control\"\n        (change) = \"typeChange()\"\n        [(ngModel)] = \"control.config.type\"\n      >\n        <option value=\"text\">Alphanumeric</option>\n        <option value=\"number\">Numeric Only</option>\n        <option value=\"email\">Email Address</option>\n      </select>\n    </div>\n  </div>\n</div>\n<!-- Common Component -->\n<div class=\"type-details\">\n  <common-editor [control]=\"control\"></common-editor>\n</div>\n<!-- Common Component-->\n<div class=\"type-details\">\n  <span class=\"form-label\" [class.no-margin]=\"control.config.showHelp === false\">Help Text:</span>\n  <div class=\"switch\">\n    <label>\n      <input type=\"checkbox\" name=\"properties-checkbox\"\n             class=\"show-check\"\n             [checked]=\"control.config.showHelp\"\n             (change)=\"onChangeDescription(control);callGA('HELPTEXT',control)\"\n             data-size=\"mini\" data-on-color=\"blue\"\n      >\n      <span class=\"lever\"></span>\n    </label>\n  </div>\n  <div class=\"col-xs-12 no-padding text-group div-check\"\n       *ngIf=\"control.config.showHelp\"\n  >\n    <textarea class=\"form-text question-textarea\" [(ngModel)] = \"control.props.helpText\"></textarea>\n  </div>\n</div>\n<div class=\"type-details\" *ngIf=\"jsonBuilderHelper.getJSONBuilt().template == 'one-page-card'\">\n    <span class=\"form-label\" [class.no-margin]=\"control.imageVisible === false\">Reference Image:</span>\n    <div class=\"switch\">\n        <label>\n            <input type=\"checkbox\" name=\"properties-checkbox\" class=\"show-check\" [checked]=\"control.imageVisible\" (change)=\"control.imageVisible = !control.imageVisible\" data-size=\"mini\" data-on-color=\"blue\">\n            <span class=\"lever\"></span>\n        </label>\n    </div>\n    <div class=\"landing-inputfile div-check\" *ngIf=\"control.imageVisible\">\n      <div class=\"col-xs-12 no-padding\">\n        <div class=\"input-group\">\n        <input\n          type=\"text\"\n          class=\"file-input\"\n          [(ngModel)]=\"control.imageURL\"\n          placeholder=\"No file\"\n          readonly\n        >\n        <label class=\"input-group-btn\">\n          <span class=\"btn btn-default btn-flat btn-basic2 btn-upload\" data-fp-apikey=\"ANQWcFDQRUiGfBqjfgINQz\" (click)=\"uploadImage(control);\">\n          UPLOAD <input type=\"\"  style=\"display: none;\">\n          </span>\n        </label>\n        </div>\n      </div>\n    </div>\n</div>\n<div class=\"type-details\">\n  <div class=\"col-xs-12 no-padding text-group horz-group\">\n    <span class=\"col-md-4 col-xs-4 no-padding form-label\">Placeholder:</span>\n    <div class=\"col-md-7 col-xs-7 no-padding horz-control\">\n      <input type=\"text\" class=\"form-text\" [(ngModel)] = \"control.config.placeholder\">\n    </div>\n  </div>\n  <span *ngIf = \"control.config.type === 'text'\">\n        <div class=\"col-xs-12 no-padding text-group  horz-group\">\n            <span class=\" col-md-4 col-xs-4 no-padding form-label\">Min. Characters:</span>\n            <div class=\"col-md-7 col-xs-7 no-padding horz-control\">\n                <input type=\"text\" class=\"form-text\" [(ngModel)] = \"control.props.minVal\">\n            </div>\n        </div>\n    </span>\n  <span *ngIf = \"control.config.type === 'number'\">\n        <div class=\"col-xs-12 no-padding text-group horz-group \">\n            <span class=\"col-md-4 no-padding form-label\">Min Value:</span>\n            <div class=\"col-md-4 col-xs-4 no-padding horz-control\">\n                <input type=\"text\" class=\"form-text\" [(ngModel)]=\"control.props.minVal\">\n            </div>\n        </div>\n        <div class=\"col-xs-12 no-padding text-group horz-group \">\n            <span class=\"col-md-4 no-padding form-label\">Max Value:</span>\n            <div class=\"col-md-4 col-xs-4 no-padding horz-control\">\n                <input type=\"text\" class=\"form-text\" [(ngModel)]=\"control.props.maxVal\">\n            </div>\n        </div>\n    </span>\n</div>\n\n<div class=\"type-details\">\n  <label class=\"check\">\n    <input type=\"checkbox\" value=\"Value1\" name=\"checkbox1\" id=\"check1\"\n           [checked]=\"control.config.validations.required.status\"\n           (change)=\"onChange(control);callGA('MANDATE',control)\"\n    >\n    <label for=\"check1\"> </label>\n    Mark as Mandatory\n  </label>\n</div>\n"

